<script>
export default {
  name: 'ImagePage',
  data() {
    return {
      images: [
        {
          title: "首页效果",
          url: "https://picsum.photos/600/400?random=1",
          desc: "首页加载完成截图",
          loading: true
        },
        {
          title: "详情页面",
          url: "https://picsum.photos/600/400?random=2",
          desc: "商品详情展示效果",
          loading: true
        },
        {
          title: "用户界面",
          url: "https://picsum.photos/600/400?random=3",
          desc: "用户个人中心界面设计",
          loading: true
        },
        {
          title: "设置页面",
          url: "https://picsum.photos/600/400?random=4",
          desc: "系统设置页面布局",
          loading: true
        },
        {
          title: "搜索结果",
          url: "https://picsum.photos/600/400?random=5",
          desc: "搜索结果显示界面",
          loading: true
        }
      ],
      previewVisible: false,
      currentIndex: 0
    }
  },
  methods: {
    goBack() {
      this.$goBackOrHome()
    },
    fetchImages() {
      // 模拟获取图片数据
      console.log("重新加载图片数据");
    },
    imageLoaded(index) {
      this.$set(this.images[index], 'loading', false);
    },
    imageError(index) {
      this.$set(this.images[index], 'url', 'data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="%233b82f6"><path d="M19 5v14H5V5h14m0-2H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm-2.5 12.5h-9v-1h9v1zm0-3h-9v-1h9v1zm0-3h-9v-1h9v1z"/></svg>');
      this.$set(this.images[index], 'loading', false);
    },
    openPreview(index) {
      this.currentIndex = index;
      this.previewVisible = true;
      // 禁止背景滚动
      document.body.style.overflow = 'hidden';
    },
    closePreview() {
      this.previewVisible = false;
      // 恢复背景滚动
      document.body.style.overflow = '';
    },
    prevImage() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
      }
    },
    nextImage() {
      if (this.currentIndex < this.images.length - 1) {
        this.currentIndex++;
      }
    }
  },
  mounted() {
    this.fetchImages();
  },
  beforeDestroy() {
    // 组件销毁时确保恢复滚动
    document.body.style.overflow = '';
  }
}
</script>

